import {Field} from "formik";
import styles from './InputChechbox.module.css';

export const InputCheckbox = ({name, child}) => {
  return (
    <div className={`${styles.checkboxAuthWrapper}`}>
      <Field
        type='checkbox'
        name={name}
        className={styles.checkboxAuthItemNone}
      />
      <span className={styles.checkboxAuthSpan}>
        <div className={styles.checkboxAuthItemBlock}>
          <div className={styles.checkboxAuthItem_1_1}></div>
        </div>
        <div className={styles.checkboxAuthName}>{child}</div>
      </span>
    </div>
  );
};