import styles from '../AuthForm.module.css';
import {validateSingIn} from "../Validates/validate";
import {Form, Formik} from "formik";
import {InputText} from "../Inputs/InputText/InputText";
import {InputPassword} from "../Inputs/InputPassword/InputPassword";
import {InputCheckbox} from "../Inputs/InputCheckbox/InputCheckbox";

export const LoginForm = () => {

  const submit = (values) => {
    console.log(values)
  };

  let buttonAuthDisable = false;

  return (
    <Formik
      initialValues={{
        email: '',
        password: '',
        rememberMe: false,
      }}
      onSubmit={submit}
      validate={validateSingIn}
    >
      {({
          errors,
          touched,
        }) => {

        return (
          <Form className={styles.wrapper}>
            <InputText
              errors={errors.email}
              touched={touched.email}
              name='email'
              type='email'
              placeholder='Email...'
            />
            <InputPassword
              errors={errors.password}
              touched={touched.password}
              name='password'
              type='password'
              placeholder='...'
            />
            <InputCheckbox
              name='rememberMe'
              child=' '
            />
            <button
              type="submit"
              disabled={buttonAuthDisable}
            ></button>
          </Form>
        )
      }}
    </Formik>
  );
};